* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  overflow-x: hidden;
  scroll-behavior: smooth;
  background-color: #002738 !important;
}

.background-dark {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-color: rgb(10, 1, 18);
  position: relative;
}

.form-login {
  width: 100%;
  max-width: 300px;
  padding: 20px;
  background-color: rgb(10, 1, 18);
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -30%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  border: 1px solid #efefef;
  border-radius: 8px;
}

.form-login input {
  padding: 10px 13px;
  width: 260px !important;
  border-radius: 7px;
}

.form-login button {
  width: 100%;
  background-color: rgb(15, 23, 92);
  border: none;
  outline: none;
  padding: 13px 25px;
  color: #fff;
  font-weight: 600;
  border-radius: 7px;
}

:focus-visible {
  outline: none !important;
}

.css-zxtyt4-MuiPaper-root-MuiAppBar-root {
  background-color: #002738 !important;
}

a {
  text-decoration: none !important;
}

li {
  list-style: none !important;
}

.app-mt-70 {
  margin-top: 70px;
}

.form-login .form-password {
  padding: 10px 13px;
  width: 217px !important;
  border-radius: 0px !important;
  outline: none !important;
  border-top-left-radius: 7px !important;
  border-bottom-left-radius: 7px !important;
}

.input-group-text {
  display: flex;
  align-items: center;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: center;
  white-space: nowrap;
  background-color: #fff;
  border: 1px solid #ced4da;
  border-top-right-radius: 7px !important;
  border-bottom-right-radius: 7px !important;
}
